.circleDiv {
  position: absolute;
  top: 125px;
  right: 15px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: blue;
  border-radius: 50%;
  opacity: 0.5;
  background-color: grey;
  -webkit-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}

.circleDiv.colored {
  opacity: 1;
  background: rgb(161, 140, 209);
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(161, 140, 209, 1)),
    to(rgba(251, 194, 235, 1))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(161, 140, 209, 1) 0%,
    rgba(251, 194, 235, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(161, 140, 209, 1) 0%,
    rgba(251, 194, 235, 1) 100%
  );
}
.colorPicker {
  position: absolute;
  top: 125px;
  right: 75px;
  width: 50px;
  height: 100px;
  border: 3px solid black;
  opacity: 0;
  -webkit-transition: opacity 0.1s ease-in-out;
  transition: opacity 0.1s ease-in-out;
}
.colorPicker.show {
  opacity: 1;
  -webkit-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}

