.toolbar {
  position: absolute;
  top: 50%;
  padding: 15px;
  margin-top: -12.5em;
  margin-left: 1em;
  background: #1f1b2c;
  background: -webkit-gradient(linear, left bottom, left top, from(#1f1b2c), to(#352e4c));
  background: -webkit-linear-gradient(bottom, #1f1b2c, #352e4c);
  background: linear-gradient(to top, #1f1b2c, #352e4c);
  color: #e6f4fd;
  width: 5em;
  height: 25em;
  max-width: 25em;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  border-radius: 2em; }
  .toolbar__icon {
    cursor: pointer;
    font-size: 2em;
    -webkit-transition: all 100ms cubic-bezier(0.61, 0.01, 0.43, 0.99);
    transition: all 100ms cubic-bezier(0.61, 0.01, 0.43, 0.99); }
    .toolbar__icon:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      -webkit-transition: all 100ms cubic-bezier(0.61, 0.01, 0.43, 0.99);
      transition: all 100ms cubic-bezier(0.61, 0.01, 0.43, 0.99); }
    .toolbar__icon.is-active {
      -webkit-animation: bump 200ms;
              animation: bump 200ms; }
      .toolbar__icon.is-active.icon--home {
        color: #ffc600; }
      .toolbar__icon.is-active.icon--chart {
        color: #00ff36; }
      .toolbar__icon.is-active.icon--smile {
        color: #24a1fd; }
      .toolbar__icon.is-active.icon--bell {
